import Vue from 'vue';
import Vuex from 'vuex';


Vue.use(Vuex);
const store = new Vuex.Store({
    state:{
        isShowMoreMenus:false, // actionsheet
        isLogined:true, //  
        loginname:'', // 

      unLoginedMoreMenus:{
        login:'' // 
      },

      isLoginedMoreMenus:{
        center:'', // 
        msg:'',
        logout:'',
      },

      topics:[
        {name:'all',desc:''}, // 
        {name:'good',desc:''},
        {name:'share',desc:''},
        {name:'ask',desc:''},
        {name:'job',desc:''},
        {name:'test',desc:''},
      ]
    },
    getters:{
        moreMenus(state){
            // storestate
            if(state.isLogined){ //  / 
              return state.isLoginedMoreMenus;
            }
            return state.unLoginedMoreMenus;
        }
    },
    mutations:{
        // statemutation
        mutationIsShowMoreMenus:function(state,payload){
            state.isShowMoreMenus=payload;
            
        },
        // 
        mutationIsLogined:function(state){
          state.isLogined=true;
        },
        mutationLoginname:function(state,payload){
          state.loginname=payload;
        },
        mutationLogout:function(state){
            isLogined=false;
            state.loginname='';
            state.accesstoken='';
        }
    },
    actions:{}
})


export default store;


